/**
 * @ngdoc Component
 * @name LambdaLauncher.component.parameters
 *
 * @module LambdaLauncher
 *
 * @description
 * parameters component - A component to configure settings of an application
 *
 * @author Mohan Singh ( gmail::mslogicmaster@gmail.com, skype :: mohan.singh42 )
 */
(function () {
  'use strict';
  angular
    .module('LambdaLauncher')
    .component('parameters', {
      templateUrl: 'app/components/functions/parameters/parameters.html',
      bindings: {
        function: '<',
        onRunLaunch: '&'
      },
      controller: [
        '$log',
        '$stateParams',
        'Lambda',
        '$state',
        'toaster',
        '_',
        'blockUI',
        '$rootScope',
        '$scope',
        'Analytics',
        function ($log, $stateParams, Lambda, $state, toaster, _, blockUI, $rootScope, $scope, Analytics) {
          var $ctrl = this, function_id;

          /**
           * component's lifeCycle hooks
           */
          $ctrl.$onInit = initialization;
          $ctrl.$onChanges = onChanges;

          /**
           * public methods
           */
          $ctrl.updateParameters = updateParameters;
          /**
           * public properties
           */

          /**
            * @function
            * @name initialization
            * @description
            * A component's lifeCycle hook which is called after all the controllers on an element have
            * been constructed and had their bindings initialized
            */
          function initialization() {
            function_id = $ctrl.function.iD;
            if (!function_id) {
              function_id = $stateParams.function_id;
              if (!function_id)
                throw Error('function_id not found');
            }

            $ctrl.parameters = angular.copy(Lambda.getParameters(function_id));
            $ctrl.isSaveParamEnable = Lambda.isSaveParamEnable(function_id);
          }

          function onChanges(bindings) {
            $log.info('Parameters', bindings);
            if (!bindings.function.isFirstChange()) {
            }

          }
          /**
           * @function updateParameters
           * @description update parameters value
           * 
           * @returns void
           */
          function updateParameters() {
            launchFunction($ctrl.function, $ctrl.parameters);
            Lambda.enableSaveParams($ctrl.function.iD, $ctrl.isSaveParamEnable);
            saveParams();
          }
          function clearParamValues() {
            if ($ctrl.isSaveParamEnable) {
              return;
            }
            _.forEach($ctrl.parameters, function (p) {
              p.value = '';
            })
          }

          function saveParams() {
            var params = angular.copy($ctrl.parameters);
            Lambda
              .saveParameters(function_id, params)
              .then(function () {
                $ctrl.form.$setPristine();
                $log.info('Parameters updated.');
              });
          }

          function launchFunction(func, params) {
            var funcId = func.iD, parameters;

            if (params) {
              parameters = params;
            } else {
              parameters = Lambda
                .getParameters(funcId);
            }

            params = {};
            _.forEach(parameters, function (p) {
              params[p.name.trim()] = p.value;
            });
            blockUI.start();
            Analytics.track('ll_runfunction');
            Lambda
              .runFunction(func.name, params)
              .then(function (response) {
                saveRun(funcId, {
                  Payload: response.data,
                  StatusCode: response.status
                });
                toaster.success('Success', 'Function was executed successfully.');
                $log.info('Function run successful', response);
                blockUI.stop();
                clearParamValues();
              }, function (error) {
                toaster.error('Error', 'Error occurred while executing function.');
                $log.error('Function run error', error);
                saveRun(funcId, {
                  Payload: error.data.errorMessage || error.data,
                  StatusCode: error.status
                });
                blockUI.stop();
              });
          }

          function saveRun(funcId, resultSet) {
            $log.info(resultSet);
            var runInstance = angular.copy(resultSet);
            runInstance.executionTime = _.now();
            Lambda.saveRun(funcId, runInstance);

            if (_.isFunction($ctrl.onRunLaunch)) {
              $ctrl.onRunLaunch();
            }
          }

          var LambdaLauncherEvent = $rootScope.$on('EVENT:LAUNCH_FUNCTION', function (e, d) {
            if (d.function.iD == $ctrl.function.iD) {
              updateParameters();
            }
          });
          $scope.$on('$destroy', function () {
            LambdaLauncherEvent();
          });

          //end of controller
        }]
    })
})();

/**
 * @ngdoc Component
 * @name LambdaLauncher.component.functionsList
 *
 * @module LambdaLauncher
 *
 * @description
 * Lambda functions list component - Manage applications Lambda functions
 *
 * @author Mohan Singh ( gmail::mslogicmaster@gmail.com, skype :: mohan.singh42 )
 */
(function () {
  'use strict';
  angular
    .module('LambdaLauncher')
    .component('functionsList', {
      bindings: {
        onSelect: '&'
      },
      templateUrl: 'app/components/functions/list/functions.list.html',
      controller: [
        'Lambda',
        '$log',
        '$state',
        '_',
        'toaster',
        'blockUI',
        'ENV_CONFIG',
        '$injector',
        'App',
        '$scope',
        function (Lambda, $log, $state, _, toaster, blockUI, ENV_CONFIG, $injector, App, $scope) {
          var $ctrl = this;

          /**
           * component's lifeCycle hooks
           */
          $ctrl.$onInit = initialization;

          /**
           * public methods
           */
          $ctrl.getRandomColor = getRandomColor;
          $ctrl.selectFn = selectFn;
          /**
           * public properties
           */
          $ctrl.App = App;

          /**
            * @function
            * @name initialization
            * @description
            * A component's lifeCycle hook which is called after all the controllers on an element have
            * been constructed and had their bindings initialized
            */
          function initialization() {
            $ctrl._ = _;
            $ctrl.$state = $state;
            getFunctions();
          }

          function getFunctions() {
            var params = {
              pageSize: 200,
              filter: [
                {
                  fieldName: "actionType",
                  operator: "equals",
                  value: "Function"
                }
              ]
            };
            blockUI.start();
            Lambda
              .getFunctions(params)
              .then(function (data) {
                blockUI.stop();
                functionsHandler(data);
              }).catch(function (error) {
                blockUI.stop();
                $log.error(error);
                /**
                 * @todo 
                 * This has to be moved to authInterceptor
                 * authInterceptor does not catch response error of a XHR request which is invoked by Bankand.invoke()
                 * discussed with @relly
                 */
                $injector.get('$state').go(ENV_CONFIG.ROUTE_LOGIN_STATE, { error: $base64.encode(error.data), app: $state.params.app }, { reload: true });
                $injector.get('Auth').logout();
              });
          }

          function functionsHandler(data) {
            var functions = data || [];
            $ctrl.functions = functions;
            if (functions.length > 0) {
              updateFunctionParameters(functions);
              if ($ctrl.$state.params.function_id) {
                var fn = _.find($ctrl.functions, { iD: Number($ctrl.$state.params.function_id) });
                if (fn && !App.isSmallDevice()) {
                  App.setDetailView(false);
                  selectFn(fn);
                }
              }
            }
            if (!$ctrl.$state.params.function_id) {
              if (!App.isSmallDevice()) {
                App.setDetailView(false);
                selectFn($ctrl.functions[0]);
              }
            }
          }

          function updateFunctionParameters(functions) {
            _.forEach(functions, function (f) {
              $log.info('function', f);
              var funcParams, params, storedParams;
              funcParams = Lambda.getParameters(f.iD);
              params = _.trim(f.inputParameters) ? _.split(f.inputParameters, ',') : '';
              storedParams = _.map(funcParams, function (f) {
                return f.name;
              });

              if(_.isEmpty(f.inputParameters)){
                 Lambda
                  .saveParameters(f.iD, []);
              }else if ((!_.isEmpty(f.inputParameters) && !funcParams) || !_.isEqual(params, storedParams)) {
                $log.info('inputParameters', f);
               
                params = _.map(params, function (p) {
                   var value = _.find(funcParams, { key : _.camelCase(p) });
                  return {
                    name: p,
                    value: _.get(value,'value') || '',
                    key: _.camelCase(p)
                  }
                });
                Lambda
                  .saveParameters(f.iD, params);
              }
            });
          }

          function getRandomColor() {
            var letters = '0123456789ABCDEF';
            var color = '#';
            for (var i = 0; i < 6; i++) {
              color += letters[Math.floor(Math.random() * 16)];
            }
            return color;
          }

          function selectFn(fn) {
            if (App.isSmallDevice()) {
              App.setDetailView(true);
            }
            $state.go('dashboard.appFunctions.detail', { function_id: fn.iD });
          }

          $scope.$watch(function () {
            return Lambda.getRuns();
          }, function (newV) {
            $ctrl.runs = angular.copy(newV);
          }, true)
          //end of controller
        }]
    });
})();

/**
 * @ngdoc Component
 * @name LambdaLauncher.component.functionDetail
 *
 * @module LambdaLauncher
 *
 * @description
 * Lambda function detail component
 *
 * @author Mohan Singh ( gmail::mslogicmaster@gmail.com, skype :: mohan.singh42 )
 */
(function () {
  'use strict';
  angular
    .module('LambdaLauncher')
    .component('functionDetail', {
      bindings: {
      },
      templateUrl: 'app/components/functions/detail/functions.detail.html',
      controller: [
        '$log',
        '_',
        '$stateParams',
        'Lambda',
        'blockUI',
        'toaster',
        '$rootScope',
        'App',
        '$detectViewPort',
        '$scope',
        function ($log, _, $stateParams, Lambda, blockUI, toaster, $rootScope, App, $detectViewPort,$scope) {
          var $ctrl = this,
            functionId = $stateParams.function_id;

          /**
           * component's lifeCycle hooks
           */
          $ctrl.$onInit = initialization;
          /**
           * public methods
           */
          $ctrl.launchFunction = launchFunction;
          $ctrl.onRunLaunch = onRunLaunch;
          $ctrl.back = back;
          /**
           * public properties
           */
          $ctrl.$detectViewPort = $detectViewPort;
          $ctrl.activeTab = 0;
          /**
            * @function
            * @name initialization
            * @description
            * A component's lifeCycle hook which is called after all the controllers on an element have
            * been constructed and had their bindings initialized
            */
          function initialization() {
            getFunction();
            getRuns();
          }
          function getRuns() {
            $ctrl.runs = Lambda.getRun(functionId);
          }
          function getFunction() {
            $ctrl.function = Lambda
              .getFunction(functionId) || {};
          }

          function onRunLaunch() {
            $ctrl.activeTab = 1;
            getRuns();
          }

          function launchFunction(fn) {
            $rootScope.$emit('EVENT:LAUNCH_FUNCTION', {
              function: angular.copy(fn)
            })
          }

          function back() {
            App.setDetailView(false);
          }

          var eventHandler = $rootScope.$on('EVENT:ON_LOAD_FUNCTIONS', function () {
            getFunction();
          })

          $scope.$on('$destroy', function () {
            eventHandler();
          });
          //end of controller
        }]
    });
})();

