/**
 * @ngdoc Component
 * @name LambdaLauncher.component.signin
 * @module LambdaLauncher
 *
 * @description
 * signin component - A application landing page
 * @requires $log, $state, $uibModal, Auth, ENV_CONFIG, blockUI, $injector
 * 
 * @author Mohan Singh ( gmail::mslogicmaster@gmail.com, skype :: mohan.singh42 )
 */
(function () {
  'use strict';
  angular
    .module('LambdaLauncher')
    .component('signin', {
      templateUrl: 'app/components/signin/signin.html',
      controller: [
        '$log',
        '$state',
        '$uibModal',
        'Auth',
        'ENV_CONFIG',
        'blockUI',
        '$injector',
        'toaster',
        'App',
        function ($log, $state, $uibModal, Auth, ENV_CONFIG, blockUI, $injector, toaster, App) {
          var $ctrl = this;

          /**
           * component's lifeCycle hooks
           */
          $ctrl.$onInit = initialization;

          /**
           * public methods
           */
          $ctrl.socialSignin = socialSignin;
          $ctrl.signin = signin;
          $ctrl.forgotPassword = forgotPassword;
          /**
           * public properties
           */
          $ctrl.isSigning = false;
          $ctrl.isAuthenticated = false;
          /**
            * @name initialization
            * @description
            * A component's lifeCycle hook which is called after all the controllers on an element have
            * been constructed and had their bindings initialized
            */
          function initialization() {
            $ctrl.appName = $state.params.app;
            $ctrl.providers = angular.copy(App.socialProviders);
            $ctrl.error = $injector.get('$stateParams').err ? $base64.decode($injector.get('$stateParams').err) : '';
            getSocialProviders();
          }

          /**
           * @name getSocialProviders
           * @description fetch enabled social providers from backand
           * @returns void
           */
          function getSocialProviders() {
            Auth
              .getSocialProviders()
              .then(function (response) {
                $ctrl.socialProviders = response.data;
                $log.log('Social Provider collection', response.data);
              }, function (error) {
                //handle error
                $log.error(error);
              });
          }

          /**
           * @name socialSignin
           * @description signin with social providers[facebook,github ...]
           * @param {string} provider A name of social provider[facebook,github ...]
           * @returns void
           */
          function socialSignin(provider) {
            Auth
              .socialSignin(provider)
              .then(function () {
                $state.go(ENV_CONFIG.ROUTE_HOME_STATE, { app: $state.params.app || '' });
              }, function (error) {
                //handle error
                $log.error(error);
                toaster.error(error.data.error_description);
              });
          }

          /**
           * @name signin
           * @description signin with credentails[username,password]
           * @requires $ctrl.user which contains [username,password]
           * @returns void
           */
          function signin() {
            blockUI.start;
            Auth
              .signin($ctrl.user)
              .then(function (response) {
                blockUI.stop();
                $log.info(response);
              }, function (error) {
                blockUI.stop();
                $log.error(error);
                toaster.error(error.data.error_description);
              });
          }

          /**
           * @name forgotPassword
           * @description opens Twitter Bootstrap modal
           * @returns void
           */
          function forgotPassword() {
            $uibModal.open({
              component: 'requestResetPassword',
              backdrop: 'static',
              resolve: {
              }
            });
          }

        }]
    });
})();

/**
 * @ngdoc Component
 * @name LambdaLauncher.component.header
 *
 * @module LambdaLauncher
 *
 * @description
 * header component - A application header
 * @requires $log, Auth, App, ENV_CONFIG, $state,blockUI
 * @author Mohan Singh ( gmail::mslogicmaster@gmail.com, skype :: mohan.singh42 )
 */
(function () {
  'use strict';
  angular
    .module('LambdaLauncher')
    .component('appHeader', {
      templateUrl: 'app/components/header/header.html',
      bindings: {
        isAuthenticated: '<'
      },
      controller: [
        '$log',
        'Auth',
        'App',
        'ENV_CONFIG',
        '$state',
        'blockUI',
        function ($log, Auth, App, ENV_CONFIG, $state, blockUI) {
          var $ctrl = this;

          /**
           * component's lifeCycle hooks
           */
          $ctrl.$onInit = initialization;
          /**
           * public methods
           */
          $ctrl.logout = logout;
          $ctrl.back = back;
          /**
           * public properties
           */

          /**
            * @name initialization
            * @description
            * A component's lifeCycle hook which is called after all the controllers on an element have
            * been constructed and had their bindings initialized
            */
          function initialization() {
            $log.info('header component initialized');
            $ctrl.App = App;
            $ctrl.appName = $state.params.app;
            $ctrl.isLoggedIn = Auth.isLoggedIn;
            $ctrl.currentUser = Auth.currentUser;
          }

          /**
           * @name logout
           * @description logout current user
           * @param {object} $event An click event object
           * @returns void
           */
          function logout($event) {
            $event.preventDefault();
            blockUI.start();
            Auth.logout().then(function () {
              blockUI.stop();
              $state.go(ENV_CONFIG.ROUTE_LOGIN_STATE, {app: $state.params.app || ''}, { reload: true });
            }, function (error) {
              blockUI.stop();
              $log.error('Error on logout - ', error);
              $state.go(ENV_CONFIG.ROUTE_LOGIN_STATE, {app: $state.params.app || ''}, { reload: true });
            });
            $log.info('logout called');
          }

          /**
           * @name back
           * @description navigate to last state 
           * @returns void
           */
          function back() {
            $log.info(App.state);
            var to, toParams = {};
            to = App.state.from;
            toParams = App.state.fromParams;
            if (to.name === '') {
              to = 'dashboard.appFunctions';
              toParams = {app: $state.params.app || ''};
            } else if (to.name === ENV_CONFIG.ROUTE_LOGIN_STATE && $ctrl.isLoggedIn()) {
              return;
            }
            $state.go(to, toParams);
          }

          //end of controller
        }]
    })
})();

