(function () {
  window.$base64 = (function $base64() {
    var PADCHAR = '=';

    var ALPHA = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789+/';

    function getbyte64(s, i) {
      var idx = ALPHA.indexOf(s.charAt(i));
      if (idx === -1) {
        throw "Cannot decode base64";
      }
      return idx;
    }

    function decode(s) {
      // convert to string
      s = "" + s;
      var pads, i, b10;
      var imax = s.length;
      if (imax === 0) {
        return s;
      }

      if (imax % 4 !== 0) {
        throw "Cannot decode base64";
      }

      pads = 0;
      if (s.charAt(imax - 1) === PADCHAR) {
        pads = 1;
        if (s.charAt(imax - 2) === PADCHAR) {
          pads = 2;
        }
        // either way, we want to ignore this last block
        imax -= 4;
      }

      var x = [];
      for (i = 0; i < imax; i += 4) {
        b10 = (getbyte64(s, i) << 18) | (getbyte64(s, i + 1) << 12) |
          (getbyte64(s, i + 2) << 6) | getbyte64(s, i + 3);
        x.push(String.fromCharCode(b10 >> 16, (b10 >> 8) & 0xff, b10 & 0xff));
      }

      switch (pads) {
        case 1:
          b10 = (getbyte64(s, i) << 18) | (getbyte64(s, i + 1) << 12) | (getbyte64(s, i + 2) << 6);
          x.push(String.fromCharCode(b10 >> 16, (b10 >> 8) & 0xff));
          break;
        case 2:
          b10 = (getbyte64(s, i) << 18) | (getbyte64(s, i + 1) << 12);
          x.push(String.fromCharCode(b10 >> 16));
          break;
      }
      return x.join('');
    }

    function getbyte(s, i) {
      var x = s.charCodeAt(i);
      if (x > 255) {
        throw "INVALID_CHARACTER_ERR: DOM Exception 5";
      }
      return x;
    }

    function encode(s) {
      if (arguments.length !== 1) {
        throw "SyntaxError: Not enough arguments";
      }

      var i, b10;
      var x = [];

      // convert to string
      s = "" + s;

      var imax = s.length - s.length % 3;

      if (s.length === 0) {
        return s;
      }
      for (i = 0; i < imax; i += 3) {
        b10 = (getbyte(s, i) << 16) | (getbyte(s, i + 1) << 8) | getbyte(s, i + 2);
        x.push(ALPHA.charAt(b10 >> 18));
        x.push(ALPHA.charAt((b10 >> 12) & 0x3F));
        x.push(ALPHA.charAt((b10 >> 6) & 0x3f));
        x.push(ALPHA.charAt(b10 & 0x3f));
      }
      switch (s.length - imax) {
        case 1:
          b10 = getbyte(s, i) << 16;
          x.push(ALPHA.charAt(b10 >> 18) + ALPHA.charAt((b10 >> 12) & 0x3F) +
            PADCHAR + PADCHAR);
          break;
        case 2:
          b10 = (getbyte(s, i) << 16) | (getbyte(s, i + 1) << 8);
          x.push(ALPHA.charAt(b10 >> 18) + ALPHA.charAt((b10 >> 12) & 0x3F) +
            ALPHA.charAt((b10 >> 6) & 0x3f) + PADCHAR);
          break;
      }
      return x.join('');
    }

    return {
      encode: encode,
      decode: decode
    };
  })();

})();
/**
 * @ngdoc service
 * @name LambdaLauncher.service.authInterceptor
 *
 * @module LambdaLauncher
 *
 * @description
 * A interceptor service to intercept valid request
 * 
 * @author Mohan Singh ( gmail::mslogicmaster@gmail.com, skype :: mohan.singh42 )
 */
(function () {
  'use strict';
  angular
    .module('LambdaLauncher')
    .factory('authInterceptor', authInterceptor);
  /** @ngInject */
  function authInterceptor($q, Backand, $localStorage, $injector,_) {

    return {
      requestError: function (rejection) {
        return $q.reject(rejection);
      },
      response: function (response) {
        return response;
      },
      responseError: function (rejection) {
        var envConstants = $injector.get('ENV_CONFIG');
        if (rejection.status === 401 && !Backand.isManagingRefreshToken()) {
          var errorMessage =
            Backand.getUsername() ?
              'The session has expired, please sign in again.' :
              null;
          $injector.get('$log').error(errorMessage); 
          $injector.get('$state').go(envConstants.ROUTE_LOGIN_STATE, { err: errorMessage, app: Backand.defaults.appName }, { reload: true });
          $injector.get('Auth').logout();
        }
        return $q.reject(rejection);
      }
    };
  }


})();
/**
 * @ngdoc service
 * @name LambdaLauncher.service.Auth
 *
 * @module LambdaLauncher
 *
 * @description
 * A service to manage user workflow [Authentication, Signup]
 * 
 * @author Mohan Singh ( gmail::mslogicmaster@gmail.com, skype :: mohan.singh42 )
 */
(function () {
  'use strict';

  angular
    .module('LambdaLauncher')
    .service('Auth', AuthService);
  /** @ngInject */
  function AuthService($localStorage, $rootScope, $state, Backand, ENV_CONFIG, $log, App, Analytics) {
    var self = this,
      ROUTE_HOME_STATE = ENV_CONFIG.ROUTE_HOME_STATE || 'dashboard.appFunctions',
      ROUTE_LOGIN_STATE = ENV_CONFIG.ROUTE_LOGIN_STATE || 'login';
    /**
     * @property currentUser
     * stores logged in user
     */
    self.currentUser = {};
    self.getCurrentUser = getCurrentUser;
    self.getSocialProviders = getSocialProviders;
    self.socialSignin = socialSignin;
    self.signin = signin;
    self.logout = logout;
    self.AutherizeRoutes = AutherizeRoutes;
    self.isLoggedIn = isLoggedIn;


    /**
     * @name getCurrentUser
     * @description get loggedIn user
     * redirects to ROUTE_HOME_STATE if valid user
     * 
     * @returns promise
     */
    function getCurrentUser() {
      if (!Backand.user) //in case Backand was created
        return;

      return Backand.user.getUserDetails()
        .then(function (response) {
          var data = response.data;
          self.currentUser.details = data;
          $log.info('User -', data);
          if (data !== null) {
            self.currentUser.name = data.username;
            if (ROUTE_LOGIN_STATE === $state.current.name) {
              $state.transitionTo(ROUTE_HOME_STATE, { reload: true, app: $state.params.app }, App.state.toParams);
            }
          }
        });
    }

    /**
     * @name getSocialProviders
     * @description Fetch social providers from Backand
     * 
     * @returns promise
     */
    function getSocialProviders() {
      if (!Backand.getSocialProviders)
        return;

      return Backand.getSocialProviders()
        .then(function (response) {
          return response;
        });
    }


    /**
     * @name socialSignin
     * @description Signin to Backand with Social Providers[facebook, github ....]
     * 
     * @param {string} provider [facebook, github ....]
     * 
     * @returns promise
     */
    function socialSignin(provider) {
      return Backand.socialSignin(provider)
        .then(function (response) {
          onSignin(response.data);
          return response.data;
        });
    }


    /**
     * @name signin
     * @description Signin to Backand with credentials
     * 
     * @param {object} User credentials[username,password] 
     * @returns promise
     */
    function signin(credentials) {
      return Backand.signin(credentials.username, credentials.password)
        .then(function (response) {
          onSignin(response.data);
          return response.data;
        });
    }

    /**
     * @name logout
     * @description logout from backand
     * 
     * @returns promise
     */
    function logout() {
      return Backand.signout().then(function (response) {
        clearSession();
        return response;
      }, function (error) {
        clearSession();
        return error;
      });
    }
    /**
     * @description helper function to clear currentUser's clearSession
     */
    function clearSession() {
      delete $localStorage.BACKAND_user;
      angular.copy({}, self.currentUser);
    }


    /**
     * @description check if user is loggedIn
     * @returns boolean
     */
    function isLoggedIn() {
      return self.currentUser.name ? true : false;
    }

    /**
     * @name AutherizeRoutes
     * @description $stateChangeStart listner to apply logics before state complete
     * unregister listner $stateChangeStart
     * 
     */
    function AutherizeRoutes() {
      var unregisterListner = $rootScope.$on('$stateChangeStart', verifyRoute);
      $rootScope.$on('$destroy', unregisterListner);
    }

    function verifyRoute(event, to, toParams, from) {
      /**
       * @todo route authetication goes here
       */
    }

    function onSignin(data) {
      Analytics.identify(data.fullName, data.username);
      getCurrentUser();
    }

    //end of service  
  }
})();
/**
 * @ngdoc service
 * @name LambdaLauncher.service.App
 *
 * @module LambdaLauncher
 *
 * @description
 * App service is used to share data globaly
 * 
 * 
 * @author Mohan Singh ( gmail::mslogicmaster@gmail.com, skype :: mohan.singh42 )
 */
(function () {
  'use strict';

  angular
    .module('LambdaLauncher')
    .service('App', AppService);
  /** @ngInject */
  function AppService($detectViewPort) {
    var self = this,
    functionDetailView = false;
    self.pageTitle = '';
    /**
     * social Providers
     */
    self.socialProviders = {
      google: {
        label: 'Google',
        className: 'google'
      },
      github: {
        label: 'Github AUth',
        className: 'github'
      },
      facebook: {
        label: 'Facebook',
        className: 'facebook'
      },
      twitter: {
        label: 'Twitter',
        className: 'twitter'
      },
      adfs: {
        label: 'Adfs',
        className: 'windows'
      },
      azuread: {
        label: 'Azure AD',
        className: 'windows'
      }
    }

    self.setDetailView = function(flag){
      functionDetailView = flag;
    };
    self.getDetailView = function(){
      return functionDetailView;
    };

    self.isSmallDevice = function(){
      return $detectViewPort.viewPortWidth <= 768;
    }
  }

})();

/**
 * @ngdoc service
 * @name LambdaLauncher.service.Analytics
 *
 * @module LambdaLauncher
 *
 * @description
 * Analytics service is used to call segment
 * 
 * 
 * @author Mohan Singh ( gmail::mslogicmaster@gmail.com, skype :: mohan.singh42 )
 */
(function () {
  'use strict';

  angular
    .module('LambdaLauncher')
    .service('Analytics', AnalyticsService);
  /** @ngInject */
  function AnalyticsService() {
    var self = this;

    self.identify = identify;
    self.track = track;

    /**
     *
     * @param fullName
     * @param email
     */
    function identify(fullName, email) {
      if (analytics) {
        analytics.identify(email, {
          name: fullName,
          email: email,
          createdAt: new Date().getTime()
        });
      }
      if(window.JacoRecorder){
        window.JacoRecorder.identify(email, function callback(err){});
      }
    }

    /**
     *
     * @param eventName
     * @param eventObject
     */
    function track(eventName, eventObject) {
      if (analytics) {
        analytics.track(eventName, eventObject);
      }
      if(window.JacoRecorder){
        window.JacoRecorder.push(['session.setAttribute', {
          attributeName: eventName, attributeValue: eventObject
        }, function (err) {
          if (err) {
            console.error('error setting attribute');
          }
        }]);
      }
    }

  }

})();

/**
 * @ngdoc constant
 * @name LambdaLauncher.config
 *
 * @module LambdaLauncher
 *
 * @description
 * setup application routes
 * @todo move component specific routes to component directory
 * 
 * @author Mohan Singh ( gmail::mslogicmaster@gmail.com, skype :: mohan.singh42 )
 */
(function () {
  'use strict';

  angular
    .module('LambdaLauncher')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {

    $urlRouterProvider.when('/{app:[0-9a-zA-Z]{1,}}', '/{app:[0-9a-zA-Z]{1,}}/functions');
    $stateProvider
      .state('login', {
        url: '/{app:[0-9a-zA-Z]{1,}}/login?err&t',
        component: 'signin',
        data: {
          title: 'Login'
        }
      })
      .state('default', {
        url: '/',
        component: 'default',
        data: {
          title: 'Home Page'
        }
      })
      .state('reset-password', {
        url: '/{app:[0-9a-zA-Z]{1,}}/reset-password?token',
        component: 'resetPassword',
        data: {
          title: 'Reset Password'
        }
      })
      .state('dashboard', {
        absolute: true,
        url: '/{app:[0-9a-zA-Z]{1,}}',
        data: {
          requiresLogin: true
        },
        template: '<ui-view></ui-view>'
      })
      .state('dashboard.appFunctions', {
        url: '/functions?t',
        component: 'functions',
        data: {
          requiresLogin: true,
          title: 'Launch Functions'
        }
      }).state('dashboard.appFunctions.detail', {
        url: '/:function_id',
        views: {
          'function-detail': {
            component: 'functionDetail'
          }
        },
        data: {
          requiresLogin: true,
          title: 'Launch Functions'
        }
      });

    $urlRouterProvider.otherwise(function ($injector, $location) {
      var state;
      state = $injector.get('$state');
      state.go('default', { url: $location.path() }, { location: true });
    });
  }

})();

